
.footer {
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
    grid-gap: 20px;
    padding-top: 50px;
}

.footerItems li {
    list-style-type: none;
    padding-bottom: 5px;
    text-transform: uppercase;
}

.footerLi {
    color:#676c7a;
    font-size: 10px;
    font-weight: 900;

}

.footerHead {
    font-weight: 700;
    font-size: 14px;
    color:white;
    margin-bottom: 30px;
}

.footerHead {
    border: 5px solid red;
    border-top:none;
    border-right: none;
    border-bottom: none;
    padding: 5px;
}


@media (max-width: 400px) {
    .footer {
        grid-template-columns: 100%;
    }
}