.BG {
    background-image: url(./components/img/BG.png);
    width: 100%;
    height: 100%;
}
.content {
    width: 100%;
    max-width: 943px;
    margin: 0 auto;
    color: white;
    padding-top: 85px;
    font-family: 'Open Sans', sans-serif;
}


.contentLogo {
    width: 100%;
    max-width: 943px;
    margin: 0 auto;
}

.logo {
    position: absolute;
}
@media (max-width: 500px) {
    .logo {
        max-width: 100px;
    }
}

@media (max-width: 620px) {
    .logo {
        max-width: 150px;
    }
}

@media (max-width: 400px) {
    .logo {
        max-width: 150px;
        padding-left: 25%;
    }
}