.content {
    background-color: rgba(23,25,30, .5);
    padding: 30px;
    margin-left:20px;
    margin-right:20px;
    margin-top: 59px;
}

.bigWords {
    font-size: 31px;
    text-transform: uppercase;
    font-weight: 900;
}

.underbigWords {
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
    font-size: 25px;
    margin-top: -45px;
}

.row1 {
    display: grid;
    grid-template-columns: 70% 25%;
    grid-gap: 50px;

}

.textHead {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 900;
}

.text {
    font-size: 10px;
    margin-top:25px;
    margin-bottom:25px;
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
}

.button {
    width: 77px;
    line-height: 42px;
    background-color: #bd3404;
    color:white;
    font-weight: 700;
    border:none;
}

.row2 {
    display: grid;
    grid-template-columns: 50% 50%;
    padding-top: 40px;
    grid-gap: 20px;
    margin:0 auto;
}

.imgRow2 {
    max-width: 400px;
}

.row3 {
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
    padding-top: 40px;
    grid-gap: 20px;
}
.imgRow3 {
    max-width: 250px;
}

.row4 {
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    padding-top: 40px;
    grid-gap: 20px;
}

@media (max-width: 940px) {
    .row1 {
        grid-template-columns: 95%;
    }
    .row2 {
        grid-template-columns: 95%;
        margin:0 auto;
    }
    .row4 {
        grid-template-columns: 45% 45%;
    }
}

@media (max-width: 840px) {
    .gridItems {
        grid-template-columns: 45% 45%;
    }
    .row3 {
        grid-template-columns: 45% 45%;
    }
}

@media (max-width: 700px) {
    .imgRow1 {
        max-width: 500px;
    }
    .row3 {
        grid-template-columns: 95%;
    }
}

@media (max-width: 620px) {
    .imgRow1 {
        max-width: 400px;
    }
    .imgRow2 {
        max-width: 400px;
    }
    .row4 {
        grid-template-columns: 95%;
    }
}

@media (max-width: 470px) {
    .imgRow1 {
        max-width: 300px;
    }

    .imgRow2 {
        max-width: 200px;
    }

}

@media (max-width: 400px) {
    .text {
        display: none;
    }
    .imgRow1 {
        max-width: 200px;
    }

    .imgRow3 {
        max-width: 200px;
    }


}