.container {
    margin-top: 59px;
    background-color: rgba(23,25,30, .5);
    padding: 31px;
    padding-bottom: 100px;
    margin-right: 20px;
}


.bigWords {
    font-size: 31px;
    text-transform: uppercase;
    font-weight: 900;
}

.underbigWords {
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
    font-size: 25px;
    margin-top: -45px;
}


.gridItems {
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-gap: 20px;
}

.textHead {
    color:white;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 900;
}

.text {
    color: #676c7a;
    font-size: 10px;
    max-width: 270px;
    font-weight: 900;
}

@media (max-width: 940px) {
    .image {
        max-width: 200px;
    }
}

@media (max-width: 840px) {
    .gridItems {
        grid-template-columns: 45% 45%;
    }
}

@media (max-width: 620px) {
    .gridItems {
        grid-template-columns: 98%;
    }
}


@media (max-width: 400px) {
    .text {
        display: none;
    }
}