.OurTeam {
    background-color: rgba(23,25,30, .5);
    padding: 20px;
    margin-bottom:20px;
}



.bigWords {

    font-size: 31px;
    text-transform: uppercase;
    font-weight: 900;
}

.underbigWords {
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
    font-size: 25px;
    margin-top: -45px;
}

.OurTeam {
    padding: 30px;
    margin-top: 20px;
}

.OurGrid {
    display: grid;
    grid-template-columns: 24% 24% 24% 24%;
    margin: 0 auto;
}

.name {
    text-transform: uppercase;
    font-weight: 800;
    font-size: 14px;
}

.text {
    text-transform: uppercase;
    color: #676c7a;
    font-size: 10px;
    max-width: 192px;
    font-weight: 900;
}

@media (max-width: 940px) {
    .imageFace {
        max-width: 250px;
    }
    .OurGrid {
        grid-template-columns: 45% 45%;
    }
}


@media (max-width: 770px) {

    .imageFace {
        max-width: 200px;
    }
}


@media (max-width: 620px) {
    .imageFace {
        max-width: 150px;
    }
    .main {
        grid-template-columns: 90%;
    }
}

@media (max-width: 400px) {
    .text {
        display: none;
    }
    .OurGrid {
        grid-template-columns: 98%;
    }
}