.NavBar {

}

.NavbarItems {
    display: flex;
    justify-content: flex-end;
    font-weight: 700;
    text-align: center;
    background-color: #18191f;
    padding: 29px;
    max-width: 940px;
}
.NavbarItems nav {
    padding-left: 30px;
}
@media (max-width: 940px) {
    .NavbarItems {
        max-width: 750px;
        font-size: 10px;
    }
    .NavbarItems nav {
        padding-left: 10px;
    }
}

@media (max-width: 620px) {
    .NavbarItems {
        justify-content: center;
        font-size: 15px;
    }
    
}

.Items {
    text-decoration: none;
    color:white;
}


.activeLink {
    color: #bd3404;
    border-bottom: 2px solid #bd3404;
    padding: 10px;
    transition: .5s;
}