.page {
    display: grid;
    grid-template-columns: 68% 28%;
    grid-gap: 35px;

}

.content {
    background-color: rgba(23,25,30, .5);
    padding-top: 1px;
    padding-left: 31px;
    padding-right: 31px;
    padding-bottom: 31px;
    margin-top: 59px;
}
.Post {
    border-bottom: 1px solid #676c7a;
    padding-bottom: 30px;
    padding-top: 30px;
}


.bigWords {

    font-size: 31px;
    text-transform: uppercase;
    font-weight: 900;
}

.underbigWords {
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
    font-size: 25px;
    margin-top: -45px;
}


.desTop {
    text-transform: uppercase;
    color:white;
    font-size: 10px;
}

.desBottom {
    text-transform: uppercase;
    font-size: 10px;
    color: #676c7a;
}

.description {
    display: grid;
    grid-template-columns: 43px 180px 1fr;
    grid-gap: 8px;
}

.date {
    background-color: #bd3404;
    color:white;
    text-align: center;
    line-height: 17px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 700;
}

.gridContent {
    display: grid;
    grid-template-columns: 60% 35%;
    grid-gap: 20px;
}

.contentGridtext1 {
    color:#676c7a;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 700;
}

.contentGridtext2 {
    color: #676c7a;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 700;
}


.more {
    line-height: 42px;
    color:white;
    width: 77px;
    height: 42px;
    background-color: #bd3404;
    text-align: center;
    border: none;
    font-size: 14px;
}

@media (max-width: 940px) {

}

@media (max-width: 840px) {

}

@media (max-width: 700px) {
    .gridContent {
        grid-template-columns: 95%;
    }
}

@media (max-width: 620px) {

}

@media (max-width: 470px) {
    .img {
        max-width: 300px;
    }
}

@media (max-width: 400px) {
    .img {
        max-width: 200px;
    }
    .contentGridtext1 {
        display: none;
    }
    .contentGridtext2 {
        display: none;
    }
}