.main {
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
    grid-gap: 20px;
    padding-top: 59px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
}

.gridItemElements {
    background-color: rgba(23,25,30, .5);
}


.padding {
    padding-right: 28px;
    padding-left: 28px;
}

.bigWords {

    font-size: 31px;
    text-transform: uppercase;
    font-weight: 900;
}

.underbigWords {
    text-transform: uppercase;
    color: #676c7a;
    font-weight: 900;
    font-size: 25px;
    margin-top: -45px;
}


.textItems {
    text-transform: uppercase;
    font-size: 10px;
    color: #676c7a;
    padding-top: 10px;
    font-weight: 900;
    padding-right: 15px;
}

.padding div span {
    color:white;
}

.gridItemElements1 {
    padding-rigt: 28px;
    padding-left: 28px;
    padding-bottom: 80px;
    background-color: rgba(23,25,30, .5);
    padding-right: 28px;
}


.secondGridItems {
    display: grid;
    grid-template-columns: 17% 83%;
    grid-gap: 28px;
    padding-right: 15px;
}

.text {
    text-transform: uppercase;
    font-size: 10px;
    color: #676c7a;
    max-width: 182px;
    height: 100%;
    font-weight: 900;
}

.Num {
    line-height: 42px;
    color:white;
    width: 43px;
    height: 42px;
    background-color: #bd3404;
    text-align: center;
}

.grid3text {
    font-weight: 900;
    font-size: 10px;
    color: #676c7a;
    text-transform: uppercase;
}
.gridItemElements1 p span {
    color:white;
}

.gridItemElements1 ul li {
    list-style: none;
    text-transform: uppercase;
    font-size: 10px;
    padding-bottom: 10px;
    font-weight: 900;
    color: #676c7a;
}

@media (max-width: 940px) {
    .bigWords {
        font-size: 25px;
    }
    .underbigWords {
        font-size: 15px;
        padding-top: 20px;
    }
    .image {
        max-width: 250px;
    }
}


@media (max-width: 770px) {
    .bigWords {
        font-size: 23px;
    }
    .underbigWords {
        font-size: 17px;
        padding-top: 20px;
    }
    .image {
        max-width: 200px;
    }
    .main {
        grid-template-columns: 45% 45%;
    }
}


@media (max-width: 620px) {
    .image {
        max-width: 150px;
    }
    .main {
        grid-template-columns: 90%;
    }
}

@media (max-width: 520px) {

}

@media (max-width: 400px) {
    .textItems {
        display: none;
    }
    .grids {
        height: 10%;
    }
}